@import "modules/color";
@import "modules/efects";
@import "modules/fonts";

#tp_index {
  @include bg_azul;
  height: 15px; }

#input-space {
  margin-top: 10px; }

#input-helper {
  text-align: right;
  @include poiretOne;
  font-size: 18px;
  font-weight: bold;
  color: #999999;
  margin-top: 10px;
  margin-bottom: 20px;
  padding-right: 30px; }

#input-helper a {
  text-decoration: none;
  @include font_amarelo; }

#input-helper a:hover {
  @include font_azul; }

#social-icon {
  font-size: 42px;
  padding-right: 10px;
  text-decoration: none; }

#social-icon:hover {
  @include font_amarelo; }

#nav-style {
  @include bg_azul; }

#nav-menus li {
  width: 160px;
  font-size: 20px;
  text-align: center;
  @include poiretOne;
  height: 60px;
  line-height: 60px; }

#nav-menus li a {
  @include font_amarelo; }

#nav-menus li a:hover {
  color: aliceblue; }

// Drop Down Automatico
.dropdown:hover>.dropdown-menu {
  display: block; }
